import React from 'react'

export const Intro = () => {
  return (
    <div className='profession prices' id="about">
      <h3>BEMUTATKOZÁS</h3>
      <div className="pricesText">
        <p>Dr. Jeszenka Ádám ügyvéd vagyok, a Szegedi Tudományegyetem Állam- és Jogtudományi Karán, jogász szakon szereztem a diplomámat. Ügyvédi praxisom Budapesten, a XIII. kerületben található a Dózsa György út metrómegálló közvetlen szomszédságában.</p>
         
        <p>A Pázmány Péter Katolikus Egyetem Jog- és Államtudományi Karán gazdasági jogi szakjogászi képesítést szereztem, tanulmányaimat pedig jelenleg a sportjogi szakjogászi kurzuson folytatom. Képviselem, hogy a mai bonyolult és összetett gazdasági életben a jogi és gazdasági kérdések és problémák kizárólag együttesen kezelhetők és oldhatók meg.</p>
          
        <p>Kiemelten fontosnak tartom megbízóim igényeinek kiemelkedő szakmai színvonalon történő gyors, precíz és alapos támogatását, a kreatív, lendületes és lelkiismeretes ügyintézést, amely elengedhetetlen az állandóan változó jogi - üzleti környezetben.</p>
      </div>
    </div>
  )
}
